body {
  font-family: 'Open Sans', sans-serif;
  padding: 0;
  margin: 0;
  line-height: 1.6;
}
input, button {
  appearance: none;
  background: none;
  border: none;
  outline: none;
}

.App {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #53565a;
}

.form {
  display: block;
  padding: 20px;
  background-color: #f8f8f8;
  border-radius: 8px;
}

.form .form-inner {
  color: #888;
  font-size: 28px;
  margin-bottom: 30px;
}

.form .form-inner .form-group {
  display: block;
  width: 300px;
  margin-bottom: 15px;
}

.form-inner .form-group label {
  display: block;
  color: #666;
  font-size: 12px;
  margin-bottom: 5px;
  transition: 0.4s;
}

.form-inner .form-group:focus-within label {
  color: #fe4880;
}

.form .form-inner .form-group input {
  display: block;
  width: 70%;
  padding: 10px 15px;
  background-color: #f8f8f8;
  border-radius: 8px;
  transition: 0.4s;
  box-shadow: 0px 0px 0px 2px rgba(0,0,0,0.2);
}

.form .form-inner button {
  display: inline-block;
  padding: 10px 15px;
  border-radius: 8px;
  background-color: #888;
  background-size: 200%;
  background-position: 0%;
  transition: 0.4s;
  color: #fff;
  font-weight: 700;
  cursor: pointer;
}

.error-message {
  color: tomato;
}