.slider-container {
  position: relative;
}

.slider-container img {
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.left-btn {
  position: absolute;
  top: 50%;
  left: 5%;
  cursor: pointer;
}

.right-btn {
  position: absolute;
  top: 50%;
  right: 5%;
  cursor: pointer;
}